<style >
/* ivew的修改部分 */
    .layout{
        /* border: 1px solid #d7dde4; */
        background: #f5f7f9;
        position: relative;
        border-radius: 4px;
        overflow: hidden;
    }
    .layout-nav{
        /* width: 420px;
        margin: 0 auto;
        margin-right: 20px; */
    }
    .layout-footer-center{
        text-align: center;
    }
/* end iview的修改部分 */
/* 自定义部分 */
	.top :first-child {
		display: inline-block;
		position: relative;
		font-size: 20px;
		/* margin: 50px 0 5px; */
		letter-spacing: 3px;
	}
	.top :last-child {
		margin-bottom: 15px;
	}
	.top :first-child::before {
		content: '';
		display: block;
		position: absolute;
		left: -80px;
		top: 21px;
		width: 55px;
		height: 3px;
		background-color: white;
	}
	.top :first-child::after {
		content: '';
		display: block;
		position: absolute;
		right: -80px;
		top: 21px;
		width: 55px;
		height: 3px;
		background-color: white;
	}
	.alogrithm .top p {
		color: #989898;
	}
	.alogrithm .top :first-child::before {
		background-color: black;
	}
	.alogrithm .top :first-child::after {
		background-color: black;
	}
    .top {
        height: 60px;
        line-height: 60px;
    }
    .container-power {
        position: relative;
        margin: 40px 0;
        min-height: 300px;
    }
    .card-con {
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 2;
        display: flex;
        justify-content: center;
        align-items: center;
        background: rgba(183, 183, 183, 0.6)
    }
    .close-set {
        text-align: right;
        height: 26px;
        line-height: 26px;
    }
    .botton {
        width: 200px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        margin: 10px auto 0 auto;
    }
    .stockFile {
        width: 100%;
        height: 100%;
        position: absolute;
        opacity: 0;
        left: 0;
        top: 0;
    }
/* end自定义部分 */

</style>
<template>
    <div class="layout">
        <Layout :style="{ position: 'relative' }">
            <Content :style="{padding: '0', background: '#fff', minHeight: '500px'}">
                <section class="container-power">
                    <div class="card-con" v-if="secretary">
                        <Card :bordered="false" :style="{width: '300px', textAlign: 'center'}">
                            <div class="close-set">
                                <!-- <Icon type="close"  @click="hideAdd(1)"></Icon><br /> -->
                                <!-- <Button type="ghost" icon="close" :style="{ position: 'absolute', top: '14px', right: '20px' }" @click="showAdd(1)">添加入库</Button> -->
                            </div>
                            <Input v-model="addSecretary.name" placeholder="昵称" :style="{width:'200px'}"></Input><br />
                            <Select v-model="addSecretary.location" :style="{width:'200px',margin: '4px 0', textAlign:'left'}">
                                <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                            </Select>
                            <Input v-model="addSecretary.departmentName" placeholder="部门" :style="{width:'200px',margin: '4px 0'}"></Input><br />
                            <Input v-model="addSecretary.email" placeholder="邮箱" :style="{width:'200px',margin: '4px 0'}"></Input><br />
                            <div class="botton">
                                <Button type="error" size="small" @click="hideAdd(1)">关闭</Button>
                                <Button type="success" size="small" @click="add(1)">新增</Button>
                            </div>
                        </Card>
                    </div>
                    <div class="top">
                        <span>成员管理</span>
                        
                    </div>
                    <Button type="ghost" icon="plus" :style="{ position: 'absolute', top: '14px', right: '138px' }" >批量添加
                        <input type="file" class="stockFile" @change="uploadstocks($event)"/>
                    </Button>
                    <Button type="ghost" icon="plus" :style="{ position: 'absolute', top: '14px', right: '20px' }" @click="showAdd(1)">添加成员</Button>
                    
                    <div class="table">
                        <Table  :columns="columns" :data="data"></Table>
                    </div>
                </section>
            </Content>
        </Layout>
    </div>
</template>
<script>
	import {mapActions} from 'vuex';


    // end画图
	export default {
		name:'number',
		props:['number'],
		data(){
            return {
                secretary: false,
                hleper: false,
                columns: [
                    {
                        title: '名字',
                        key: 'name',
                        render: (h, params) => {
                            return h('div', [
                                h('Icon', {
                                    props: {
                                        type: 'person'
                                    }
                                }),
                                h('strong', params.row.name)
                            ]);
                        }
                    },
                    {
                        title: '地区',
                        key: 'location'
                    },
                    {
                        title: '部门',
                        key: 'departmentName'
                    },
                    {
                        title: '邮箱',
                        key: 'email'
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            // console.log(params)
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove('' +parseInt(params.row.userId))
                                        }
                                    }
                                }, '删除')
                            ]);
                        }
                    }
                ],
                data: [
                    {
                        name: 'John Brown',
                        location: '北京',
                        email: 'New York No. 1 Lake Park',
                        department: 'jishu',
                        userId: 1,
                    },
                    {
                        name: 'John Brown',
                        location: '北京',
                        email: 'New York No. 1 Lake Park',
                        userId: 1
                    },
                    {
                        name: 'John Brown',
                        location: '北京',
                        email: 'New York No. 1 Lake Park',
                        userId: 1
                    },
                    {
                        name: 'John Brown',
                        location: '北京',
                        email: 'New York No. 1 Lake Park',
                        userId: 1
                    },
                ],
                helps: [],
                addNumber: {
                    name: '',
                    location: '',
                    departmentName: '',
                    email: '',
                    roleId: 3
                },
                cityList: [
                    {
                        value: '广州',
                        label: '广州'
                    },
                    {
                        value: '北京',
                        label: '北京'
                    },
                    {
                        value: '深圳',
                        label: '深圳'
                    },
                    {
                        value: '成都',
                        label: '成都'
                    }
                ],
            }
        },
        mounted() {
            this.init()

        },
		computed:{

		},
		methods: {            
            ...mapActions('user', [
                'getPower', 'addSec', 'deletePeople'
            ]),
            //初始化调用的函数
            remove (index) {
            },
            showAdd (e) {
                if (e === 1) {
                    //单独添加一个成员
                    addOne()
                } else {
                    //批量添加
                }
            }

        },

        created() {

        }
	}
</script>
<style scoped>

</style>



