<style scoped src="../style/index.css"></style>
<style>
.layout {
    overflow: hidden;

    position: relative;

    background: #f5f7f9;
}
.layout-logo {
    float: left;

    position: relative;
    top: 15px;
    left: 20px;
/* background: #5b6270; */

    border-radius: 3px;
    width: 100px;
    height: 30px;

    flex-grow: 1;
}
.layout-nav {
    /* width: 420px; */
    margin: 0 228px;
    /* margin: 0 auto; */
    /* margin-right: 20px; */
}
.user-info {
    display: flex;

    position: absolute;
    top: 20px;
    right: 228px;
}
.user-info img {
    width: 20px;
    height: 20px;
}
.user-info div {
/* background-color: white; */
    margin: 0 0 0 20px;
    height: 20px;

    line-height: 20px;

    color: #fff;
}
/* 兼容ie11 */
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    .ie11 {
        height: 93vh;
    }
}
@font-face {
    font-family: 'Emoji';
    src: url('../../../assets/seguiemj.ttf') format('truetype');
}
body {
    font-family: 'Segoe UI Emoji', 'Emoji', Tahoma, Geneva, 'Simsun';
}
input, button, textatea, select {
    font-family: 'Segoe UI Emoji', 'Emoji', Tahoma, Geneva, 'Simsun';
}

</style>
<template>
    <div class="layout" :style="{ borderRadius: '0' }">
        <Layout :style="{minHeight: '100vh'}">
            <Header :style="{ height: '7vh\9', backgroundColor: '#595959' }">
                <div class="layout-logo">
                    <img src="../../../assets/images/WhiteEmblem.png"/>
                </div>
                <div class="layout-nav" :style="{width: '600px',backgroundColor: '#595959'}">
                    <Menu mode="horizontal" theme="dark"  :style="{backgroundColor: '#595959'}" :active-name="contral" @on-select="changeMune($event)">
                        
                            <MenuItem name="1-1">
                                <Icon type="ios-navigate"></Icon>
                                全部订单
                            </MenuItem>
                            <MenuItem name="1-2">
                                <Icon type="ios-keypad"></Icon>
                                仓库管理
                            </MenuItem>
                            <MenuItem name="1-3" >
                                <Icon type="ios-analytics"></Icon>
                                查看日志
                            </MenuItem>
                            <MenuItem name="1-4"  >
                                <Icon type="ios-paper"></Icon>
                                权限设置
                            </MenuItem>
                        
                    </Menu>
                </div>
                <div class="user-info" v-if="false">
                    <img src="../../../assets/images/a.png"/>
                    <div>{{ userName }}</div>
                </div>
                <Button type="error" :style="{ position: 'absolute',right: '4%',padding: '6px 10px',top: '14px', background: 'none', borderColor: '#fff'}" @click="loginOutS">注销</Button>
            </Header>
            <Layout :style="{flexGrow: '1', height: '93vh\9'}" class="ie11" id="layout">
                <!-- <Sider hide-trigger :style="{background: '#fff'}"> -->
                    <!-- <Menu :active-name="contral" theme="light" width="auto" @on-select="changeMune($event)">
                            <MenuItem name="1-1">全部订单</MenuItem>
                            <MenuItem name="1-2" >仓库管理</MenuItem>
                            <MenuItem name="1-3" v-if=" user.role == 6">查看日志</MenuItem>
                            <MenuItem name="1-4" v-if=" user.role == 6">权限设置</MenuItem>
                            
                    </Menu> -->
                <!-- </Sider> -->
                
                <Layout :style="{padding: '24px 24px 24px'}">
                    <Content :style="{padding: '24px', minHeight: '280px', background: '#fff',minWidth: '1200px'}">
                        <order v-if="contral == '1-1'"></order>
                        <list v-else-if="contral == '1-2'"></list>
                        <daliy v-else-if="contral == '1-3'"></daliy>
                        <power v-else-if="contral == '1-4'"></power>
                        <!-- <number ></number> -->
                    </Content>
                </Layout>
            </Layout>
        </Layout>
    </div>
</template>
<script>
	
import {mapState, mapActions} from 'vuex'
import order from './order.vue'
import list from './list.vue'
import daliy from './daliy.vue'
import power from './power'
import number from './number'
	export default {
		name: 'index',
		components:{order, list, daliy, power, number},
		data(){
			return {
                contral: '1-1',
                isCollapsed: false,
                userName: null
			}
        },
        mounted() {
            this.loginInit()
        },
		computed: {

			// 从这里引入 vueX 的 state 的变量到组件中用
			...mapState('user' ,{
				// 这里用到的是 es6 的函数写法
                'user': state => {
                    return state
				},
            }),
		},
		methods:{
			...mapActions('user', [
                'login','getInfo', 'loginout'
            ]),
            
            //一开始判断用户是否有跳过登录的权限
			loginInit(){
                let that = this
				this.getInfo().then((data)=>{
                    if (data.login == false) {
                        location.href = '../../../wxg/html/login.html'
                    }
                    that.userName = data.userName
				}).catch(err => {
					this.$Notice.error({
                        title: '初始化失败请刷新',
                        desc: err
                    });
				})
            },
            collapsedSider () {
                this.$refs.side1.toggleCollapse();
            },
            //改变订单的选择
            changeMune: function (e) {
                // console.log(e)
                this.contral = '' + e
            },
            loginOutS: function () {
                this.loginout({}, this).then((data)=>{
                    
                        location.href = '../../../wxg/html/login.html'
                    
				}).catch(err => {
					this.$Notice.error({
                        title: '请求失败失败请刷新',
                        desc: err
                    });
			})
        }

		},
		created: function () {

        },


	}
</script>

<style></style>


