<style >
/* ivew的修改部分 */
.layout {
    overflow: hidden;

    position: relative;

    border-radius: 4px;
    /* border: 1px solid #d7dde4; */

    background: #f5f7f9;
}
.layout-nav {
    /* width: 420px;
        margin: 0 auto;
        margin-right: 20px; */
}
.layout-footer-center {
    text-align: center;
}
/* end iview的修改部分 */
/* 自定义部分 */
.commom {
    display: flex;
}
.order-container {
    margin: 0 0 20px 0;

    flex-direction: column;
}
.order-header {
    border-top: 1px solid #acacad;
    border-right: 1px solid #acacad;
    border-bottom: 1px solid #acacad;
    border-left: 1px solid #acacad;
    height: 60px;

    align-items: center;
    justify-content: space-between;
}
.order-content {
    min-height: 100px;
}
.order-footer {
    border-right: 1px solid #acacad;
    border-bottom: 1px solid #acacad;
    border-left: 1px solid #acacad;
    min-height: 100px;
}
.order-item-info {
    width: 80%;
}
.order-item-info div {
    padding: 0 20px 0 10px;
}
.order-border {
    border-right: 1px solid #acacad;
}
.order-content-good {
    border-right: 1px solid #acacad;
    border-left: 1px solid #acacad;
    width: 50%;

    flex-direction: column;
}
.order-content-commom {
    border-right: 1px solid #acacad;
    border-bottom: 1px solid #acacad;
    padding: 20px;
    /* flex-grow: 1; */
    width: 25%;
}
.order-content-item {
    border-bottom: 1px solid #acacad;
    padding: 20px 20px;
    width: 100%;

    align-items: center;
}
.order-content-image {
    width: 120px;
    height: 80px;
}
.order-content-image img {
    width: 100%;
    height: 100%;
}
.order-content-info {
    padding: 14px;
    height: 100%;

    flex-direction: column;
    flex-grow: 1;
}
.order-content-info > div {
    display: flex;

    align-items: center;
    flex-grow: 1;
}
.order-content-info > div div {
    flex-grow: 1;
}
.order-content-commom > div {
    display: flex;

    height: 20px;

    line-height: 20px;
}
.order-content-commom-name {
    width: 64px;
}

.way {
    width: 100px;
}
.reject {
    color: #f01819;
}
.blue {
    color: #4b91db;
}
.green {
    color: #1aad19;
}
.no-order {
    font-size: 20px;
    text-align: center;
}
.head-name {
    font-size: 26px;

    color: #1aad19;
    background: #fff;
}
#stop-long-word {
    overflow: hidden;

    width: 60%;

    white-space: nowrap;
    text-overflow: ellipsis;
}
/* end自定义部分 */


</style>
<template>
    <div class="layout">
        <Layout :style="{ position: 'relative' }">
            <div class="head-name">全部订单</div>
            <Header :style="{ background: '#fff',width: '100%'}">
                <Input v-model="searchKey" size="large" placeholder="输入订单的信息" :style="{width: '20%', padding: '0'}" @on-keyup="searchOrder" icon="android-search"></Input>
            </Header>
            <Content :style="{padding: '22px 20px 0', background: '#fff', minHeight: '500px'}">
                <!-- 订单 -->
                <div class="order-container commom"  v-for="(order, index) in orderList" v-bind:key="order.orderId">
                    <div class="order-header commom">
                        <!-- 订单基础信息 -->
                        <div class="order-item-info commom">
                            <div class="order-border">{{ order.orderTime }}</div>
                            <div class="order-border">订单编号：{{ order.orderId }}</div>
                            <div class="order-border">物品所属部分：{{ order.goods[0].departmentName }}</div>
                            <div>物品所在地区：{{ order.goods[0].locationName }}</div>
                        </div>

                        <div class='reject way' v-if="order.orderState ===5">已驳回</div>
                        <div class='reject way' v-if="order.orderState ===3">已取消</div>
                        <div class='blue way' v-if="order.orderState === 1">待领取</div>
                        <div class='blue way' v-if="order.orderState === 0">待审核</div>
                        <div class='blue way' v-if="order.orderState === 2">待归还</div>
                        <div class='green way' v-if="order.orderState === 4">已完成</div>
                        <div class='green way' v-if="order.orderState === 6">已入库</div>
                        <!-- end订单基础信息 -->
                    </div>
                    <div class="order-content commom">
                        <div class="order-content-good commom">
                            <!-- 物品的显示 -->
                            <div class="order-content-item commom" v-for="good in order.goods" :key="good.goodsId">
                                <div class="order-content-image">
                                    <img :src=" 'https://www.wxgoffice.cn/wxg/' + good.imagePath" />
                                </div>
                                
                                <div class="order-content-info commom">
                                    <div>
                                        <div id="stop-long-word">{{good.goodsName}}</div>
                                        <div>X{{ good.goodsNumber }}</div>
                                    </div>
                                    <div v-if="good.orderType == 1">出库</div>
                                    <div v-if="good.orderType == 2">借用</div>
                                </div>
                                
                            </div>

                            <!-- end物品显示 -->
                        </div>
                        <div class="order-content-commom">
                            <div>
                                <div class="order-content-commom-name">申请人：</div>
                                <div>{{ order.applyer }} | {{ order.applyerBrunch }}</div>
                            </div>
                            <div>
                                <div class="order-content-commom-name">领取人：</div>
                                <div>{{ order.recipients }} | {{ order.recipientsBrunch }}</div>
                            </div>
                            <div>
                                <div class="order-content-commom-name">领取时间：</div>
                                <div>{{order.recipientTime}}</div>
                            </div>
                            <div>
                                <div class="order-content-commom-name">领取地点：</div>
                                <div>{{ order.recipientPlace }}</div>
                            </div>
                        </div>
                        <div class="order-content-commom">
                                                        <div>
                                <div class="order-content-commom-name">备注：</div>
                                <div>{{order.remark}}</div>
                            </div>
                            <div>
                                <div class="order-content-commom-name">留言：</div>
                                <div>{{order.comments}}</div>
                            </div>
                        </div>
                    </div>
                    <div class="order-footer" id="footer">
                        <canvas :id="index" width="" height="" ></canvas>
                        <!-- <Steps :current="1" status="error">
                            <Step title="已完成" content="这里是该步骤的描述信息"></Step>
                            <Step title="进行中" content="这里是该步骤的描述信息"></Step>
                            <Step title="待进行" content="这里是该步骤的描述信息"></Step>
                            <Step title="待进行" content="这里是该步骤的描述信息"></Step>
                        </Steps> -->
                    </div>
                </div>

                <!-- end订单 -->
                <div v-if="noOrder" class="no-order">
                    订单空空如也
                </div>
            </Content>
        </Layout>
    </div>
</template>
<script>
	import {mapActions} from 'vuex'
//增加一个orderstate和ordertype
    //画图
    let draw;
    class Draw {
        constructor(el) {
            this.el = el
            this.canvas = document.getElementById(this.el)
            this.width = document.getElementById('footer').offsetWidth
            this.height = document.getElementById('footer').offsetHeight -30
            document.getElementById(this.el).setAttribute('width', this.width);
            document.getElementById(this.el).setAttribute('height', this.height);
            this.cxt = this.canvas.getContext('2d')
            // this.stage_info = canvas.getBoundingClientRect()
            this.path = {
                beginX: 0,
                beginY: 0,
                endX: 0,
                endY: 0
            }
        }
        init (e, process, red, obj, allGreen)  {

            //速度
            let speed = Math.floor( (this.width - 160) / (obj.length - 1) )
            //点的位置
            let loactions = 80
            let  l = 0

            //字的大小
            this.cxt.font="14px Arial"
            
            // 开始初始化的图
            this.cxt.beginPath();

            // if (red = 0) {
                //线的颜色
                if (allGreen == 1) {
                    // this.cxt.strokeStyle = "#1AAD19"
                } else {
                    this.cxt.strokeStyle = "#595959"
                }
                this.cxt.moveTo(loactions, 60)
                this.cxt.lineTo(this.width-80, 60)
            // } else {
            //     //线的颜色
            //     this.cxt.strokeStyle = "#f01819"
            //     this.cxt.moveTo(loactions, 60)
            //     this.cxt.lineTo(this.width-80, 60)
            // }
            
            //循环的画出圆
            for (let i = 0; i < obj.length; i++) {
                this.cxt.arc(loactions, 60, 8, 0, Math.PI*2)
                this.cxt.fillText(obj[i].name, loactions - 30, 40)
                loactions = loactions + speed
            }
            this.cxt.stroke()
            this.cxt.closePath()

            this.cxt.beginPath()
            this.cxt.fillStyle="#1AAD19"
            this.cxt.strokeStyle = "#1AAD19"
            // 画出达到的步骤
            loactions = 80

            for (let i = 0; i < process; i++) {
                this.cxt.arc(loactions, 60, 5, 0, Math.PI*2)
                // this.cxt.arc(loactions, 60, 8, 0, Math.PI*2)
                this.cxt.fillText(obj[i].name, loactions - 30, 40)
                // this.cxt.fillText(obj[i].time, loactions - 65, 90)
                loactions = loactions + speed
                l = i
            }

            
            if (red == 1) {
                this.cxt.fill()
                this.cxt.stroke()
                this.cxt.closePath()
                this.cxt.beginPath()
                loactions = loactions - speed
                this.cxt.strokeStyle = "#f01819"
                this.cxt.arc(loactions, 60, 8, 0, Math.PI*2)
                this.cxt.closePath()
                this.cxt.stroke()
                this.cxt.beginPath()
                this.cxt.fillStyle="#f01819"
                this.cxt.strokeStyle = "#f01819"
                
                this.cxt.arc(loactions, 60, 5, 0, Math.PI*2)
                this.cxt.fillText(obj[l].name, loactions - 30, 40)
                this.cxt.fillText(obj[l].time, loactions - 65, 90)
            } else {
                // this.cxt.strokeStyle = "#1AAD19"
            }
            this.cxt.fill()
            this.cxt.closePath()
            this.cxt.beginPath()
            this.cxt.fillStyle="#1AAD19"
            this.cxt.strokeStyle = "#1AAD19"
            loactions = 80
            for (let i = 0; i < process; i++) {
                // this.cxt.arc(loactions, 60, 5, 0, Math.PI*2)
                this.cxt.arc(loactions, 60, 8, 0, Math.PI*2)
                // this.cxt.fillText(obj[i].name, loactions - 30, 40)
                // this.cxt.fillText(obj[i].time, loactions - 65, 90)
                loactions = loactions + speed
                l = i
            }
            this.cxt.stroke()
            this.cxt.closePath()
            
            if (red == 1) {
                this.cxt.beginPath()
                this.cxt.strokeStyle = "#f01819"
                this.cxt.arc(loactions - speed, 60, 8, 0, Math.PI*2)
                this.cxt.stroke()
                this.cxt.closePath()
            }



        }
    }
    // end画图
	export default {
		name:'order',
		props:['order'],
		data(){
            return {
                searchKey: '',
                orderList: [
                    //     {
                    //     applyer: "申请人",
                    //     applyerBrunch: "申请人部门",
                    //     recipients: "领取人",
                    //     recipientsBrunch: "领取人部门",
                    //     comments: "留言",
                    //     remark: "备注",
                    //     recipientPlace: "领取地点",
                    //     recipientTime: "领取时间",
                    //     orderTime: "下单时间",
                    //     orderId: "订单编号",
                    //     orderState: 1,
                    //     goods: [
                    //         {
                    //             locationName: "地区名称", // 新增加的
                    //             imagePath: "图片路径",
                    //             goodsName: "物品名称",
                    //             departmentName: "所属部门",
                    //             goodsNumber: "数量",
                    //             goodsId: "物品ID", // 用来操作物品库存
                    //             orderType: 1
                    //         }
                    //     ],
                    //     process: {
                    //         index: 0, // 0是待审核，1是待领取，2是待归还，3是已取消，4是已完成，5是被驳回
                    //         create: "创建时间",
                    //         pass: "订单通过的时间",
                    //         approval: "确认领取时间",
                    //         confirm: "确认归还时间",
                    //         complete: "订单完成时间",
                    //         cancel: "取消订单的时间",
                    //         dismiss: "被驳回的时间"
                    //     }
                    // }
                ],
                currentOrderList: [
                    //     {
                    //     applyer: "申请人",
                    //     applyerBrunch: "申请人部门",
                    //     recipients: "领取人",
                    //     recipientsBrunch: "领取人部门",
                    //     comments: "留言",
                    //     remark: "备注",
                    //     recipientPlace: "领取地点",
                    //     recipientTime: "领取时间",
                    //     orderTime: "下单时间",
                    //     orderId: "订单编号",
                    //     orderState: 1,
                    //     goods: [
                    //         {
                    //             locationName: "地区名称", // 新增加的
                    //             imagePath: "图片路径",
                    //             goodsName: "物品名称",
                    //             departmentName: "所属部门",
                    //             goodsNumber: "数量",
                    //             goodsId: "物品ID", // 用来操作物品库存
                    //             orderType: 1
                    //         }
                    //     ],
                    //     process: {
                    //         index: 5, // 0是待审核，1是待领取，2是待归还，3是已取消，4是已完成，5是被驳回
                    //         create: "创建时间",
                    //         pass: "订单通过的时间",
                    //         approval: "确认领取时间",
                    //         confirm: "确认归还时间",
                    //         complete: "订单完成时间",
                    //         cancel: "取消订单的时间",
                    //         dismiss: "被驳回的时间"
                    //     }
                    // }
                ],
                orderSearch: '',
                noOrder: false
            }
        },
        mounted() {
            this.initOrder()

        },
		computed:{

		},
		methods: {            
            ...mapActions('user', [
                'searchOrder', 'getOrder'
            ]),
            //调动画图
            paintProcess: function (process, state) {
                let orderList = this.orderList

                // 循环画图
                for (let i = 0; i < orderList.length;i++) {
                    draw=new Draw('' + i)
                    let data = this.processNumber(orderList[i].process, orderList[i].goods[0].orderType)
                    if (orderList[i].process == 4) {

                    }
                    draw.init(5, data.pop(), data.pop(), data, orderList[i].process == 4 ? 1 : 0 )
                }
            },
            //流程图中流程的数量
            processNumber: function (e ,l) {
                let data = []
                let k = 1
                let no = 0
                data.push({
                name: "创建订单",
                time: this.dealTime(e.create)
                });
                // 判断状态
                if (e.index == 0) {
                    //订单创建的状态
                    data.push({
                        name: "通过审核",
                        time: ''
                    })
                    data.push({
                        name: "确认领取",
                        time: ''
                    })
                    data.push({
                        name: "订单完成",
                        time: ''
                    })
                } else if (e.index == 1) {
                //订单在待领取阶段
                    data.push({
                        name: "通过审核",
                        time: this.dealTime(e.pass)
                    });
                    data.push({
                        name: "确认领取",
                        time: ''
                    });
                    k = 2;
                    if (l == 2) {
                        //这个订单是借用的
                        data.push({
                        name: "确认归还",
                        time: ''
                        });
                    }
                    data.push({
                        name: "订单完成",
                        time: ''
                    });
                } else if (e.index == 2) {
                    //待归还，确认领取了
                    data.push({
                        name: "通过审核",
                        time: this.dealTime(e.pass)
                    });
                    data.push({
                        name: "确认领取",
                        time: this.dealTime(e.approval)
                    });
                    data.push({
                        name: "确认归还",
                        time: ''
                    });
                    data.push({
                        name: "订单完成",
                        time: ''
                    });
                    k = 3;
                } else if (e.index == 3) {
                    //订单已经取消
                    if (e.pass) {
                        //订单通过了审核，在审核之后才取消订单
                        data.push({
                        name: "通过审核",
                        time: this.dealTime(e.pass)
                        });
                        data.push({
                        name: "已取消",
                        time: this.dealTime(e.cancel)
                        });
                        k = 3;
                    } else {
                        //订单在没有审核的时候取消
                        data.push({
                        name: "已取消",
                        time: this.dealTime(e.cancel)
                        });
                        k = 2;
                    }
                    e = 1
                    no = 1
                } else if (e.index == 4) {
                    //订单已完成
                    data.push({
                        name: "通过审核",
                        time: this.dealTime(e.pass)
                    });
                    data.push({
                        name: "确认领取",
                        time: this.dealTime(e.approval)
                    });
                    k = 4
                    if (l == 2) {
                        //这个订单是借用的
                        data.push({
                        name: "确认归还",
                        time: this.dealTime(e.confirm)
                        });
                        k = 5;
                    }
                    data.push({
                        name: "订单完成",
                        time: this.dealTime(e.complete)
                    });
                    
                } else if (e.index == 5) {
                    data.push({
                        name: "已被驳回",
                        time: this.dealTime(e.dismiss)
                    });
                    k = 2
                    e = 1
                    no = 1
                } else {
                    data.push({
                        name: "通过审核",
                        time: ''
                    });
                    data.push({
                        name: "确认领取",
                        time: ''
                    });
                    if (l == 2) {
                        //这个订单是借用的
                        data.push({
                            name: "确认归还",
                            time: '',
                        })
                    }
                    data.push({
                        name: "订单完成",
                        time: ''
                    });
                }
                data.push(no)
                data.push(k)

                return data
            },
            //对事件进行处理
            dealTime: function (time) {
                return time.slice(0, 10) + ' ' + time.slice(11, 19);
                // return time
            },
            initOrder: function () {
                let that = this
                this.getOrder().then((data) => {
                    // var data = res.data.data
                    // if (res.data.state === 200) {
                    //   context.commit(types.mutations.setInfo, res.data.data)
                    
                        if (!data) {
                            this.$Notice.warning({
                                title: '没有订单',
                            })
                            this.noOrder = true
                            return
                        }
                        for (let i = 0; i < data.orderList.length; i++) {
                            data.orderList[i].orderTime = that.dealTime(data.orderList[i].orderTime)
                        }
                        that.orderList = data.orderList
                        that.currentOrderList = data.orderList
                        that.$nextTick(() => {
                            that.paintProcess()
                        })
                    // } else if (res.data.state === 0) {
                    // resolve(res.data.data)
                    // } else {
                    // this.$Notice.error({
                    //     title: '请求失败',
                    //     desc: '请求失败请刷新页面'
                    // })
                    // }
                }).catch((err) => {
                    this.$Notice.error({
                        title: '请求错误请刷新页面',
                    });
                })
            },
            searchReq: function () {
                let that = this
                this.searchOrder({
                    data: this.orderSearch
                }, that).then((res) => {


                    var data = res.data.data
                    if (res.data.state === 200) {
                    //   context.commit(types.mutations.setInfo, res.data.data)
                    // resolve(res.data.data)
                        that.orderList = data
                        that.$nextTick(() => {
                            that.paintProcess()
                        })
                    } else {
                    that.$Notice.error({
                        title: '请求失败',
                        desc: '请求失败请重新下载'
                    })
                    }
                }).catch((err) => {
                    this.$Notice.error({
                        title: '请求错误请刷新页面',
                    });
                })
            },
            //搜索订单输入框，在输入的时候对物资的筛选
            searchOrder: function (e) {
                let arr,
                    list = this.currentOrderList;
                arr = this.search(this.searchKey);

                //修改选中的分类的属性
                // for (let i = 0; i < list.length; i++) {
                //   list[i].select = false;
                // }
                if (this.searchKey == '') {
                    arr = list;
                    // return
                }
                this.orderList = arr
                this.$nextTick(() => {
                    this.paintProcess()
                })
            },
            //模糊搜索功能
            search: function (key) {
                let len = this.currentOrderList.length,
                    arr = [],
                    reg = new RegExp(key),
                    list = JSON.parse(JSON.stringify(this.currentOrderList));
                // for (var i = 0; i < len; i++) {
                //     //如果字符串中不包含目标字符会返回-1
                //     for (let j = 0; j < list[i].goods.length; j++) {
                //         if (list[i].goods[j].goodsName.match(reg)) {
                //             arr.push(list[i]);
                //         }
                //     }
                // }
                for (var i = 0; i < list.length; i++) {
                    //如果字符串中不包含目标字符会返回-1
                    //订单编号
                    if (!list[i].orderId) {
                        break;
                    }
                    if (('' + list[i].orderId).match(reg) ) {
                        arr.push(list[i]);
                        list.splice(i, 1);
                        i--;
                    }
                }
                for (var i = 0; i < list.length; i++) {
                    //如果字符串中不包含目标字符会返回-1
                    //申请人姓名
                    if (list[i].applyer.match(reg)) {
                    arr.push(list[i]);
                    list.splice(i, 1);
                    i--;
                    }
                }
                for (var i = 0; i < list.length; i++) {
                    //如果字符串中不包含目标字符会返回-1
                    //申请人部门
                    if (list[i].applyerBrunch.match(reg)) {
                    arr.push(list[i]);
                    list.splice(i, 1);
                    i--;
                    }
                }
                for (var i = 0; i < list.length; i++) {
                    //如果字符串中不包含目标字符会返回-1
                    //领取人姓名
                    if (!list[i].recipients) {
                        break;
                    }
                    if (list[i].recipients.match(reg)) {
                        arr.push(list[i]);
                        list.splice(i, 1);
                        i--;
                    }
                }
                for (var i = 0; i < list.length; i++) {
                    //如果字符串中不包含目标字符会返回-1
                    //领取人部门
                    if (!list[i].recipientsBrunch) {
                        break;
                    }
                    if (list[i].recipientsBrunch.match(reg)) {
                        arr.push(list[i]);
                        list.splice(i, 1);
                        i--;
                    }
                }
                for (var i = 0; i < list.length; i++) {
                    //如果字符串中不包含目标字符会返回-1
                    //领取地点
                    if (!list[i].recipientPlace) {
                        break;
                    }
                    if (list[i].recipientPlace.match(reg)) {
                        arr.push(list[i]);
                        list.splice(i, 1);
                        i--;
                    }
                }
                let isSplice = false
                for (var i = 0; i < list.length; i++) {
                    //如果字符串中不包含目标字符会返回-1
                    for (let j = 0; j < list[i].goods.length; j++) {
                        //物品
                        if (!list[i].goods[j].goodsName) {
                            break;
                        }
                        if (list[i].goods[j].goodsName.match(reg)) {
                            arr.push(list[i]);
                            list.splice(i, 1);
                            isSplice = true;
                            break;
                        }
                    }
                    if (isSplice) {
                        isSplice = false;
                        i--;
                    }

                }
                for (var i = 0; i < list.length; i++) {
                    //如果字符串中不包含目标字符会返回-1
                    //备注
                    if (!list[i].remark) {
                    break;
                    }
                    if (list[i].remark.match(reg)) {
                    arr.push(list[i]);
                    list.splice(i, 1);
                    i--;
                    }
                }
                let regType;
                if (key == '出库') {
                    regType = 1
                    for (let i = 0; i < list.length; i++) {
                    //如果字符串中不包含目标字符会返回-1
                    //备注
                        if (!list[i].goods) {
                            break;
                        }
                        if (!list[i].goods[0].orderType) {
                            break;
                        }
                        if (list[i].goods[0].orderType == regType) {
                            arr.push(list[i]);
                            list.splice(i, 1);
                            i--;
                        }
                    }
                } else if (key == '借用') {
                    regType = 2
                    for (let i = 0; i < list.length; i++) {
                    //如果字符串中不包含目标字符会返回-1
                    //备注
                        if (!list[i].goods) {
                            break;
                        }
                        if (!list[i].goods[0].orderType) {
                            break;
                        }
                        if (list[i].goods[0].orderType == regType) {
                            arr.push(list[i]);
                            list.splice(i, 1);
                            i--;
                        }
                    }
                }
                return arr;
            },
        },
        created() {

        }
	}
</script>
