<style scoped>
/* ivew的修改部分 */
.layout {
    overflow: hidden;

    position: relative;

    border-radius: 4px;
    /* border: 1px solid #d7dde4; */

    background: #f5f7f9;
}
.layout-nav {
    /* width: 420px;
        margin: 0 auto;
        margin-right: 20px; */
}
.layout-footer-center {
    text-align: center;
}
.daliy-item {
    display: flex;
    border: 1px solid #595959;
    padding: 10px;
    /* height: 50px; */

    line-height: 40px;

    align-items: center;
    justify-content: space-between;
    /* border-bottom: 1px solid #acacad; */
}
.daliy-time {
    width: 15%;
}
.daliy-department {
    width: 15%;
}
.daliy-people,
.daliy-place {
    width: 7%;
}
.daliy-describ {
    width: 15%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.daliy-content {
    width: 41%;
}
.head-name {
    font-size: 26px;

    color: #1aad19;
    background: #fff;
}
/* end iview的修改部分 */

</style>
<template>
    <div class="layout">
        <Layout>
            <div class="head-name">操作记录</div>
            <Header  :style="{ background: '#fff',width: '100%', display: 'flex', justifyContent: 'space-between' }">
                <div>
                    <Button type="ghost" icon="arrow-down-a" v-on:click="downLoad">导出</Button>
                </div>
            </Header>
            <Content :style="{padding: '0 50px', background: '#fff',}">
                <div class="daliy-container">
                    <div class="daliy-item">
                        <div class="daliy-time">时间</div>
                        <div class="daliy-department">部门</div>
                        <div class="daliy-place">地区</div>
                        <div class="daliy-people">操作人</div>
                        <div class="daliy-describ">物品描述</div>
                        <div class="daliy-content">操作内容</div>
                    </div>
                    <div class="daliy-item" v-for="item in daliyList" v-bind:key="item.logId">
                        <div class="daliy-time">{{ item.logTime }}</div>
                        <div class="daliy-department">{{ item.logDepartment }}</div>
                        <div class="daliy-place">{{ item.logLocation }}</div>
                        <div class="daliy-people">{{ item.logOperator }}</div>
                        <div class="daliy-describ">{{ item.goodsName }}</div>
                        <div class="daliy-content">{{ item.logContent }}</div>
                    </div>

                </div>
            </Content>
        </Layout>
    </div>
</template>
<script>
	import {mapActions} from 'vuex';
    export default {
        name:'daliy',
        props:['daliy'],
        data () {
            return {
                //用于显示每个小格子的内容
                daliyList:[ 
                    // {
                    //     logId:"1Id",
                    //     logTime:"日志时间",
                    //     logContent:"日志内容",
                    //     logOperator:"日志记录人",
                    //     logDepartment:"部门",
                    //     logLocation:"地区",
                    //     goodsName:"物品名称"
                    // },
                    // {
                    //     logId:"2Id",
                    //     logTime:"日志时间",
                    //     logContent:"日志内容",
                    //     logOperator:"日志记录人",
                    //     logDepartment:"部门",
                    //     logLocation:"地区",
                    //     goodsName:"物品名称"
                    // }
                ]
            }
        },
        mounted() {
            this.initDaily()
        },
        methods: {
            ...mapActions('user', [
                'download', 'getDaliy'
            ]),
            //判断该浏览器是什么浏览器
            myBrowser: function () {  
                var userAgent = navigator.userAgent; //取得浏览器的userAgent字符串  
                var isOpera = userAgent.indexOf("Opera") > -1;  
                if (isOpera) {  
                    return "Opera"  
                }; //判断是否Opera浏览器  
                if (userAgent.indexOf("Firefox") > -1) {  
                    return "FF";  
                } //判断是否Firefox浏览器  
                if (userAgent.indexOf("Chrome") > -1){  
                    return "Chrome";  
                }  
                if (userAgent.indexOf("Safari") > -1) {  
                    return "Safari";  
                } //判断是否Safari浏览器  
                if (userAgent.indexOf("compatible") > -1 && userAgent.indexOf("MSIE") > -1 && !isOpera) {  
                    return "IE";  
                }; //判断是否IE浏览器  
                if (userAgent.indexOf("Trident") > -1) {  
                    return "Edge";  
                } //判断是否Edge浏览器  
            },

            //用于下载的函数
            oDownLoad: function () {  


                this.download({

				}, this).then((res)=>{

                    if (res.data.state === 200) {
                    //   context.commit(types.mutations.setInfo, res.data.data)
                        
                        // console.log(data);
                        let oPop = window.open('https://www.wxgoffice.cn/wxg' + res.data.data, '_blank')
                            // let oPop = window.open('http://192.168.1.109:80' + data, "", "width=1, height=1, top=5000, left=5000");  
                        for(; oPop.document.readyState != "complete"; ) {  
                            if (oPop.document.readyState == "complete")break;  
                        }  
                        oPop.document.execCommand("SaveAs");  
                        // oPop.close(); 
                    } else {
                        this.$Notice.error({
                            title: '下载失败',
                            desc: '下载出现问题请重新下载'
                        })
                    }
				}).catch(err => {
					this.$Notice.error({
                        title: '下载失败',
                        desc: '下载出现问题,可能浏览器进行了拦截请重新下载'
                    });
				})
            },
            //触发下载函数
            downLoad: function () {
                this.oDownLoad()
            },

            //对表格进行初始化
            initDaily: function () {
                let that = this
                this.getDaliy({

				}).then((res)=>{
                        // console.log(data);
                    if (res.data.state === 200) {
                    //   context.commit(types.mutations.setInfo, res.data.data)
                        that.daliyList = res.data.data
                    } else {
                        this.$Notice.error({
                            title: '请求失败',
                            desc: '请刷新'
                        })
                    }
                    
				}).catch(err => {
					this.$Notice.error({
                        title: '初识化失败',
                        desc: '初识化失败请刷新 ' + err
                    });
				})
            }
        }
    }
</script>


