
<style >
/* ivew的修改部分 */
.layout {
    overflow: hidden;

    position: relative;

    border-radius: 4px;

/* border: 1px solid #d7dde4; */

    background: #f5f7f9;
}
.layout-nav {
    /* width: 420px;
        margin: 0 auto;
        margin-right: 20px; */
}
.layout-footer-center {
    text-align: center;
}
/* end iview的修改部分 */
/* 自定义部分 */
.top :first-child {
    display: inline-block;

    position: relative;

    font-size: 20px;

/* margin: 50px 0 5px; */
    letter-spacing: 3px;
}
.top :last-child {
    margin-bottom: 15px;
}
.top :first-child::before {
    display: block;

    position: absolute;
    top: 21px;
    left: -80px;

    width: 55px;
    height: 3px;

    background-color: white;

    content: "";
}
.top :first-child::after {
    display: block;

    position: absolute;
    top: 21px;
    right: -80px;

    width: 55px;
    height: 3px;

    background-color: white;

    content: "";
}
.alogrithm .top p {
    color: #989898;
}
.alogrithm .top :first-child::before {
    background-color: black;
}
.alogrithm .top :first-child::after {
    background-color: black;
}
.top {
    height: 60px;

    line-height: 60px;
}
.container-power {
    position: relative;

    margin: 40px 0;
    min-height: 300px;
}
.card-con {
    display: flex;

    position: absolute;
    z-index: 2;

    width: 100%;
    height: 100%;

    background: rgba(183, 183, 183, .6);

    align-items: center;
    justify-content: center;
    ;
}
.close-set {
    height: 26px;

    line-height: 26px;
    text-align: right;
}
.botton {
    display: flex;

    margin: 0 auto;
    margin: 10px auto 0 auto;
    width: 200px;

    justify-content: space-between;
}
.head-name {
    font-size: 26px;

    color: #1aad19;
    background: #fff;
}
/* end自定义部分 */


@font-face {
    font-family: 'Emoji';
    src: url('../../../assets/seguiemj.eot');
    src: url('../../../assets/seguiemj.eot') format('embedded-opentype'),
    url('../../../assets/seguiemj.ttf') format('truetype');
}
body {
    font-family: 'Segoe UI Emoji', 'Emoji', Tahoma, Geneva, 'Simsun';
}
input, button, textatea, select,td, tr{
    font-family: 'Segoe UI Emoji', 'Emoji', Tahoma, Geneva, 'Simsun';
}
html:root {
    body {
        font-family: Tahoma, Geneva, 'Microsoft Yahei', 'Simsun', 'Segoe UI Emoji', 'Emoji';
    }
    input, button, textarea, select,{
        font-family: Tahoma, Geneva, 'Microsoft Yahei', 'Simsun', 'Segoe UI Emoji', 'Emoji';
    }
}


</style>

