                    this.secretary = false
                } else {
                    this.hleper = false
                }
            },
            // 增加角色，这个
            add: function (e) {
                if (e == 1) {
                    this.addsecretary()
                } else {
                    this.addHelp()
                }
            },
            addsecretary: function () {
                let that = this
                
                //判断里面是否为空              
                if (this.addSecretary.email.length == 0  || this.addSecretary.location.length == 0 || this.addSecretary.name.length == 0 || this.addSecretary.nickName.length == 0) {
                    this.$Notice.error({
                        title: '你有信息没有填完',
                        desc: '请重新检查并填写'
                    })
                    return 
                }
                // console.log(this.addSecretary)
                this.addSec(this.addSecretary, this).then((res) => {
                    // this.$Notice.success({
                    //     title: '上传成功',
                    //     desc: '已经更新：' + data
                    // })
                    // that.secretary =false
                    // that.addSecretary.email = ''
                    // that.addSecretary.departmentName = 0
                    // that.addSecretary.location = 0
                    // that.addSecretary.name = ''
                    // that.init()

                    var data = res.data.data
                    if (res.data.state === 200) {
                    //   context.commit(types.mutations.setInfo, res.data.data)
                    // resolve(res.data.data)
                        this.$Notice.success({
                            title: '上传成功',
                            desc: '已经更新：' + data
                        })
                        that.secretary =false
                        that.addSecretary.email = ''
                        that.addSecretary.departmentName = ''
                        that.addSecretary.location = 0
                        that.addSecretary.name = ''
                        that.addSecretary.nickName = ''
                        that.init()
                    } else {
                    that.$Notice.error({
                        title: '请求失败',
                        desc: '请求失败请重新更新：' + res.data.data
                    })
                    }
                }).catch((err) => {
                    this.$Notice.error({
                        title: '上传失败',
                        desc: '请重新更新'
                    })
                    console.log (err)
                })
            },
            addHelp: function () {
                let that = this
                
                //判断里面是否为空              
                if (this.addHelper.email.length == 0 || this.addHelper.departmentName.length == 0 || this.addHelper.location.length == 0 || this.addHelper.name.length == 0 || this.addHelper.nickName.length == 0) {
                    this.$Notice.error({
                        title: '你有信息没有填完',
                        desc: '请重新检查并填写'
                    })
                    return 
                }
                // console.log(this.addHelper)
                this.addSec(this.addHelper, this).then((res) => {
                    // this.$Notice.success({
                    //     title: '上传成功',
                    //     desc: '已经更新库存：' + data
                    // })
                    // that.hleper =false
                    // that.addHelper.email = ''
                    // that.addHelper.departmentName = 0
                    // that.addHelper.location = 0
                    // that.addHelper.name = 0
                    // that.init()

                        var data = res.data.data
                        if (res.data.state === 200) {
                        //   context.commit(types.mutations.setInfo, res.data.data)
                        // resolve(res.data.data)
                            this.$Notice.success({
                                title: '上传成功',
                                desc: '已经更新库存：' + data
                            })
                            that.hleper =false
                            that.addHelper.email = ''
                            that.addHelper.departmentName = ''
                            that.addHelper.location = 0
                            that.addHelper.name = ''
                            that.addHelper.nickName = ''
                            that.init()
                        } else {
                            that.$Notice.error({
                                title: '请求失败',
                                desc: '请求失败请重新下载：' + res.data.data
                            })
                        }
                }).catch((err) => {
                    this.$Notice.error({
                        title: '上传失败',
                        desc: '请重新更新'
                    })
                })
            },
            deleteperson: function (data) {
                let that = this
                this.deletePeople(data, this).then((res) => {
                    // this.$Notice.success({
                    //     title: '删除成功',
                    //     desc: '已经更新：' + data
                    // })
                    // that.secretary =false
                    // that.init()

                    var data = res.data.data
                    if (res.data.state === 200) {
                    //   context.commit(types.mutations.setInfo, res.data.data)
                    // resolve(res.data.data)
                        this.$Notice.success({
                            title: '删除成功',
                            desc: '已经更新：' + data
                        })
                        that.secretary =false
                        that.init()
                    } else {
                    that.$Notice.error({
                        title: '请求失败',
                        desc: '请求失败请重新下载：' + res.data.data
                    })
                    }
                }).catch((err) => {
                    this.$Notice.error({
                        title: '删除失败',
                        desc: '请重新删除'
                    })
                    console.log(err)
                })
            }
        },
        created() {

        }
	}
</script>
